	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>the Island of Newfoundland depends on ferry service for access to goods and services and travel for visitors to or from the Island;</p>\n</li>\n<li>\n<p>weather and ice can lead and has led to significant delays;</p>\n</li>\n<li>\n<p>mechanical problems with aging ferryfleets can lead and has led to significant delay;</p>\n</li>\n<li>\n<p>such delays cause expensive disruptions to businesses, communities, individuals, and governments; and can increase the cost of goods, including food;</p>\n</li>\n<li>\n<p>Prince Edward Island was connected to mainland Canada two decades ago;</p>\n</li>\n<li>\n<p>technology has advanced significantly, as evidenced by recent construction of long-distance subsea tunnels in Norway;</p>\n</li>\n<li>\n<p>providing a fixed link between the Island of Newfoundland would better connect the province of Newfoundland and Labrador and all Canadians;</p>\n</li>\n<li>\n<p>construction of such a project would be a major economic stimulus;</p>\n</li>\n<li>\n<p>such a link would provide ongoing benefits to NL and other provinces along the transit route (e.g., Quebec) from more reliable transportation alternatives and the potential for increased tourism</p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Liberal Party of Canada call on the Government of Canada to: </p>\n<ul>\n<li>\n<p>fully document the feasibility of such a fixed link, including the attendant economic benefits arising therefrom to the province, the region, and the country, and seek proposals for construction of a fixed link;</p>\n</li>\n<li>\n<p>consider all possible funding options, including public-private partnerships, tolls, and direct funding, to achieve this goal(Newfoundland and Labrador)</p>\n</li>\n</ul>","frontmatter":{"path":"/policy/infrastructure-and-connecting-canadians-via-a-fixed-link","date":"Tue Mar 06 2018 00:08:44 GMT-0500 (EST)","title":"Infrastructure and Connecting Canadians via a Fixed Link"}}},"pathContext":{}}

